export default [
    { // 
        path: '/mine',
        component: resolve => {
            require.ensure([], () => {
                resolve(require('../views/base/mine/Main.vue'))
            })
        },
        children: [
            {
                path: 'safeset',
                name: 'mine-safeset',
                component: resolve => {
                    require.ensure([], () => {
                        resolve(require('../views/base/mine/SafeSet.vue'))
                    })
                },
                meta: {
                    title: ' - ',
                    noback: true, //  
                }
            },
            {
                path: 'profile',
                name: 'mine-profile',
                component: resolve => {
                    require.ensure([], () => {
                        resolve(require('../views/base/mine/Profile.vue'))
                    })
                },
                meta: {
                    title: ' - ',
                    noback: true
                }
            },
            {
                path: 'password',
                name: 'mine-password',
                component: resolve => {
                    require.ensure([], () => {
                        resolve(require('../views/base/mine/Password.vue'))
                    })
                },
                meta: {
                    title: ' - ',
                    noback: true
                }
            },
        ]
    },
    { // 
        path: '/feedback',
        component: resolve => {
            require.ensure([], () => {
                resolve(require('../views/base/feedback/Main.vue'))
            })
        },
        children: [
            {
                path: 'index',
                name: 'feedback-index',
                component: resolve => {
                    require.ensure([], () => {
                        resolve(require('../views/base/feedback/Index.vue'))
                    })
                },
                meta: {
                    title: ' - ',
                    noback: true
                }
            },
            {
                path: 'add',
                name: 'feedback-add',
                component: resolve => {
                    require.ensure([], () => {
                        resolve(require('../views/base/feedback/Add.vue'))
                    })
                },
                meta: {
                    title: ' - ',
                    noback: true,
                }
            },
            {
                path: 'view',
                name: 'feedback-view',
                component: resolve => {
                    require.ensure([], () => {
                        resolve(require('../views/base/feedback/View.vue'))
                    })
                },
                meta: {
                    title: ' - ',
                    noback: true,
                }
            },
        ]
    },
  
]



