// 
// 
const pathPre = '/app'
//  
export default {
    path: pathPre,
    component: resolve => {
        require.ensure([], () => {
            resolve(require('../views/app/Main.vue'))
        })
    },
    children: [
        //
        {
            path: 'setting',
            name: 'app-list',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/app/Index.vue'))
                })
            },
            meta: {
                title: '-LOGO',
                noback: true
            }
        },
        {
            path: 'boot',
            name: 'app-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/app/Boot.vue'))
                })
            },
            meta: {
                title: '-',
                noback: true
            }
        },
       
    ]
}



