<style lang='scss' rel='stylesheet/scss'>
    @import "../utils/mixins/mixins";

    .index-container {
        
        background: #d9e0e7;

        @media (max-width: 767px) {
            .header-wap {
                display: block;
            }
            .header-pc {
                display: none;
            }
            .wap-header {
                border-bottom: 1px solid #ededed;
            }
        }

        @media (min-width: 768px) {
            .header-wap {
                display: none;
            }
            .header-pc {
                display: block;
            }
        }

        .header {
            height: 55px;
            line-height: 55px;
            text-align: center;
            z-index: 11;
            background: #fff;

            .logo {
                text-align: left;
                padding-left: 28px;
                >* {
                    display: inline-block;
                    vertical-align: middle;
                }
                img {
                    width: 30px;
                    height: 30px;
                }

                h1 {
                    font-size: 16px;
                    font-weight: normal;
                }
            } // 
            @at-root {
                .header-right {
                    text-align: right;

                    >* {
                        text-align: center;
                        display: inline-block;

                        >em {
                            display: inline-block;
                            vertical-align: middle;
                        }
                    } // 
                    .feedback {
                        cursor: pointer;
                        margin: 0 5px 0 15px
                    }

                    @at-root {
                        .feedback-dropdown {
                            top: 35px !important;
                            width: 120px;
                        }
                    }

                    .nickname {
                        cursor: pointer;
                        margin-right: 30px;
                        img {
                            width: 30px;
                            height: 30px;
                            border-radius: 50%;
                            vertical-align: middle;
                            margin-right: 2px;
                        }
                    }

                    @at-root {
                        .nickname-dropdown {
                            top: 35px !important;
                            width: 160px;
                        }
                    }
                }
            }

            .title{
                white-space:nowrap;
            }
        }

        .content {
            @media (max-width: 767px) {
                margin-top: 55px;
            }

            .left-menu-container {
                position: fixed;
                width: 220px;
                z-index: 10;
                top: 56px;
                bottom: 0;
                overflow: hidden;
                overflow-y: auto;
                transition: all 300ms ease;
                transform: translateX(0); // 
                .el-menu-item,
                .el-submenu__title {
                    height: 40px;
                    line-height: 40px;
                    font-size: 14px;
                }
                @media (max-width: 767px) {
                    transform: translateX(-100%);
                    top: 0px;
                    z-index: 12;
                }

                >div.nav-title {
                    font-size: 14px;
                    color: #999;
                    margin: 12px 20px 0;
                }

                &.isShowMenue {
                    transform: translateX(0);
                }
            }

            .index-right-content {
                position: absolute;
                padding: 75px 20px 20px 240px;
                width: 100%;
                background: #d9e0e7;
                min-height: 100%;
                height: 100%;
                top: 0;
                overflow: auto;
                @media (max-width: 767px) {
                    padding: 75px 20px 20px;
                    position: absolute;
                    /*top: 110px;*/
                }

                .back-btn {
                    cursor: pointer;
                    color: #58B7FF;
                    &:hover {
                        text-decoration: underline;
                    }
                }

                .right-title {
                    font-size: 24px;
                    margin-bottom: 15px;
                    >small {
                        font-weight: 300;
                        font-size: 70%;
                        display: inline-block;
                    }
                }
            }
        }
    }
</style>

<template>
    <article class="index-container">
        <!-- pc-->
        <el-row class="header header-pc" type="flex">
            <el-col :span="10" class="logo">
                <img src="../assets/images/logo.png" @click="$router.push({name:'main'})">
                <h1   @click="$router.push({name:'main'})"></h1>
            </el-col>
            <el-col :span="6" class="title">
                <!--<h2>{{ userInfo.gov }}</h2>-->
                <h2> {{userInfo.gov_name}}</h2>
            </el-col>
            <el-col :span="8" class="header-right">
                <!--<div><i class="iconfont icon-bell"></i> <em></em></div>
                <el-dropdown trigger="click" @command="handleFeedback">
                    <span class="el-dropdown-link feedback">
                        <i class="iconfont icon-service"></i> <em></em> <i class="el-icon-caret-bottom el-icon--right"></i>
                      </span>
                    <el-dropdown-menu slot="dropdown" class="feedback-dropdown">
                        <el-dropdown-item command="index"></el-dropdown-item>
                        <el-dropdown-item command="add"></el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>-->
                <el-dropdown trigger="click" @command="handleNickname">
                    <span class="el-dropdown-link nickname">
                        <img :src="{url: userInfo.avatar, sex: userInfo.sex} | defaultAvatar"> {{userInfo.name}} <i
                              class="el-icon-caret-bottom el-icon--right"></i>
                      </span>
                    <el-dropdown-menu slot="dropdown" class="nickname-dropdown">
                        <el-dropdown-item command="profile"></el-dropdown-item>
                        <el-dropdown-item command="password"></el-dropdown-item>
                        <!--<el-dropdown-item command="safeset"></el-dropdown-item>-->
                        <el-dropdown-item :divided="true"></el-dropdown-item>
                        <el-dropdown-item command="loginout"></el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </el-col>
        </el-row>
        <!-- wap-->
        <section class="header-wap">
            <el-row class="header wap-header" type="flex">
                <el-col :span="20" class="logo" >
                    <img src="../assets/images/logo.png">
                    <h1></h1>
                </el-col>
                <el-col :span="4">
                    <i class="iconfont icon-list" @click="handleIsShowMenue(!isShowMenue)"></i>
                </el-col>
            </el-row>
            <el-row class="header" type="flex">
                <el-col :span="8" :offset="2">
                    <h2> {{userInfo.gov_name}}</h2>
                </el-col>
                <el-col :span="24" class="header-right">
                    <div><i class="iconfont icon-xiaoxizhongxin"></i> <em></em></div>
                    <div class="feedback"><i class="iconfont icon-wenti"></i> <em></em></div>
                    <el-dropdown trigger="click" @command="handleNickname">
                        <span class="el-dropdown-link nickname">
                        <img :src="{url: userInfo.avatar, sex: userInfo.sex} | defaultAvatar"><i
                              class="el-icon-caret-bottom el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown" class="nickname-dropdown">
                            <el-dropdown-item command="profile"></el-dropdown-item>
                            <el-dropdown-item command="password"></el-dropdown-item>
                            <el-dropdown-item :divided="true"></el-dropdown-item>
                            <el-dropdown-item command="loginout"></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </el-col>
            </el-row>
        </section>

        <article class="content">
            <!---->
            <el-menu :default-active="navMenueActive" class="left-menu-container" :class="{ 'isShowMenue':isShowMenue }" :unique-opened="true"
                @select="handleMenuClick" :router="true">
                <div class="nav-title"></div>
                <MenuTree v-for="item in navMenus" :data="item" :key="item.id"></MenuTree>
            </el-menu>
            <!---->
            <section class="index-right-content" @click="handleIsShowMenue(false)">
                <h2 class="right-title">
                    <i class="back-btn" v-show="isShowBack" @click="$router.back()"></i> <i v-show="isShowBack">|</i> {{mainTitle}}
                    <!--asd-->
                    <small>{{subTitle}}</small>
                </h2>

                <div class="route-content" v-loading="contentLoading">
                    <router-view></router-view>
                </div>
            </section>
        </article>
    </article>
</template>

<script>
    import * as authService from '../services/base/authService'
    import MenuTree from './component/tree/MenuTree.vue'
    import authUtils from '../utils/authUtils'
    import getMenutree from '../services/base/menutreeService.js'

    export default {
        data() {
            return {
                isShowMenue: false,
                contentLoading: false,
                mainTitle: this.$store.state.index.webpathMain,
                subTitle: this.$store.state.index.webpathSub,
                isShowBack: false,
            }
        },
        computed: {
            userInfo() {
                return authUtils.getUserInfo()
            },
            // de
            navMenus() {
                return this.$store.state.index.navMenu
            },
            // 
            navMenueActive() {
                return this.$store.state.index.navMenueActive
            }
        },
        watch: {
            '$store.state.index.webpathMain': function () {
                this.mainTitle = this.$store.state.index.webpathMain
            },
            '$store.state.index.webpathSub': function () {
                this.subTitle = this.$store.state.index.webpathSub
            }
        },
        created() {
             getMenutree().then(ret=>{
            this.$store.dispatch('setIndexNavMenu',{menu:ret});
             })
            // this.getMenutree()

            authUtils.authRefreshtoken() // token
            xmview.setContentLoading = this.setContentLoading.bind(this)
            xmview.setContentBack = this.showContentBack.bind(this)
            // this.$store.dispatch('setIndexMenuActive', this.$route.path) // 
            this.$store.dispatch('setIndexNavMenu', {menu: authUtils.getNavMenu()}) // 
        },
        mounted() {
            window.onresize = () => {
                this.handleIsShowMenue(document.documentElement.clientWidth > 767)
            }
        },
        beforeDestroy() {
            window.onresize = null
        },
        methods: {

            handleIsShowMenue(val) {
                this.isShowMenue = val
            },
            handleFeedback(type) {
                /* eslint-disable indent  */
                switch (type) {
                    case 'index':
                        this.$router.push({
                            name: 'feedback-index'
                        })
                        break
                    case 'add':
                        this.$router.push({
                            name: 'feedback-add'
                        })
                        break
                    default:
                        console.error('handleFeedback not support type')
                        break
                }
            },
            // 
            handleNickname(type) {
                /* eslint-disable indent  */
                switch (type) {
                    case 'loginout':
                        {
                            authService.loginout()
                            this.$router.push({
                                name: 'login'
                            })
                            break
                        }
                    case 'safeset':
                        {
                            this.$router.push({
                                name: 'mine-safeset'
                            })
                            break
                        }
                    case 'profile':
                        {
                            this.$router.push({
                                name: 'mine-profile'
                            })
                            break
                        }
                    case 'password':
                        {
                            this.$router.push({
                                name: 'mine-password'
                            })
                            break
                        }
                    default:
                        {
                            throw new Error('index.vue, handleNickname not support type')
                        }
                }
            },
            // 
            handleMenuClick(item) {
                this.$store.dispatch('clearFetchParam', true)
                if (item === this.$route.path) {
                    this.$router.replace({
                        name: this.$route.name,
                        query: {
                            refreshId: Date.now()
                        }
                    })
                    xmview.setContentLoading(false)
                } else {
                    this.$router.push({
                        path: item
                    })
                }
            },
            setContentLoading(loading) {
                if (!loading) {
                    this.$nextTick(() => {
                        setTimeout(() => {
                            this.contentLoading = loading
                        }, 300)
                    })
                } else {
                    this.contentLoading = loading
                }
            },
            // 
            showContentBack(isShow) {
                this.isShowBack = isShow
            },
        },
        components: {
            MenuTree
        }
    }
</script>


